import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ByProductAppComponent } from './by-product-app.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: '',
    component: ByProductAppComponent
  },
]

@NgModule({
  declarations: [ByProductAppComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class ByProductAppModule { }
